  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: none;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  overflow: hidden;
  z-index: 9999;
}

.vld-overlay.is-active {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.vld-overlay.is-full-page {
  z-index: 9999;
  position: fixed;
}

.vld-overlay .vld-background {
  bottom: 0;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  background: #fff;
  opacity: 0.5;
}

.vld-overlay .vld-icon, .vld-parent {
  position: relative;
}


.login {
  background: #20c997; /* fallback for old browsers */
  background: -webkit-gradient(linear, right top, left top, from(#20c997), to(#00c96e));
  background: linear-gradient(to left, #20c997, #00c96e);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.login .login-form-row {
  height: 100vh;
}
.center-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
}














































































































































































































































